import { useEffect } from 'react';

export function ToastContainer({ toasts, onRemove }) {
  return (
    <div className="fixed top-4 right-4 z-50 space-y-2">
      {toasts.map(toast => (
        <Toast key={toast.id} {...toast} onRemove={onRemove} />
      ))}
    </div>
  );
}

function Toast({ id, message, type, onRemove, undoAction }) {
  useEffect(() => {
    const timer = setTimeout(() => onRemove(id), 5000);
    return () => clearTimeout(timer);
  }, [id, onRemove]);

  const bgColor = {
    success: 'bg-green-500',
    error: 'bg-red-500',
    warning: 'bg-yellow-500',
    info: 'bg-blue-500'
  }[type] || 'bg-gray-500';

  return (
    <div className={`${bgColor} text-white px-6 py-4 rounded-lg shadow-xl flex items-center gap-4 min-w-[300px] animate-slide-in`}>
      <span className="flex-1 font-medium">{message}</span>
      {undoAction && (
        <button
          onClick={() => {
            undoAction();
            onRemove(id);
          }}
          className="bg-white text-gray-800 px-3 py-1 rounded-md font-bold hover:bg-gray-100 transition text-sm"
        >
          UNDO
        </button>
      )}
      <button
        onClick={() => onRemove(id)}
        className="text-white hover:text-gray-200 transition font-bold text-xl"
      >
        
      </button>
    </div>
  );
}

export default Toast;